
.detail{
  margin-top: 0.5rem;
  padding-bottom: 1.3rem;
}
.info{
  width: 100%;
  margin-bottom: 0.5rem;
  background-color: #fff;
}
.ordertime{
  width: 92%;
  margin: auto;
  border-bottom: 1px solid #dedede;
  line-height: 2.5rem;
  color: #666666;
  font-size: 0.7rem;
  overflow: hidden;
}
.teachType{
  margin-left: 0.3rem;
}
.orderInfo{
  height: 2.5rem;
