
.img{
  width: 1rem;
  height: 1.1rem;
}
body{
      background-color: #ededed;
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      color:#7f7f7f;
}
*{
    margin:0;
    padding: 0px;
}
a{
    text-decoration: none;
}
.nav{
      position: fixed;
      width: 100%;
      left: 0px;
      bottom: 0px;
      height: 2.35rem;
      z-index: 10;
      border-top: 1px solid #ededed;
}
.nav ul{
        background-color: #fff;
        list-style: none;
        margin: 0;
        padding: 0;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        -ms-flex-align: center;
            align-items: center;
}
.nav ul li{
            height: 2.35rem;
            text-align: center;
            background-color: #fff;
}
.nav ul li a{
            display: inline-block;
            width: 100%;
            height: 100%;
            text-decoration: none;
}
.nav ul li a span{
                  display: inline-block;
                  float: left;
                  width: 100%;
                  height: 1.175rem;
                  line-height: 1.175rem;
                  font-size: 0.5rem;
                  color: #bcbcbc;
}
.nav .jjj{
          color: #72abb4
}
.nav ul li a span:first-of-type{
                              position: relative;
                              margin-top: 0.3rem;
}
.nav ul li a span:last-of-type{
                                margin-top: -0.1rem;
}
.nav ul li a span:first-of-type img{
                                    position: absolute;
                                    top: 50%;
                                    left: 50%;
                                    transform: translate(-50%,-50%);
}
.nav ul li:first-of-type a span:first-of-type img{
                                                 /* width:90%;*/
                                                  height: 80%;
}
.nav ul li:last-of-type a span:first-of-type img{
                                                   /* width: 90%;*/
                                                    height: 80%;
}
.main{
      width: 100%;
      margin-bottom: 0;
}



